import {
	api,
	postajax,
	postajaxnologin
} from '@/utils/api.js';
export default {
	data() {
		return {
			content: '',
			type: '',
			isImg: '',
			srclist: [],
			videoContext: '',
			playVideo: false,
			videoSrc: ''
		}
	},

	created() {

	},
	onShow() {
		var that = this;
		that.videoSrc = '';
		
	},
	onHide() {
		/*	*/
	},
	methods: {
		/*选择视频*/
		chooseVideo: function() {
			var that = this
			wx.chooseVideo({
				maxDuration: 60,
				compressed: true,
				sourceType: ['album', 'camera'],
				success: function(res) {
					that.srclist.push({
						path: res.tempFilePath,
						type: 1
					});
					that.uploadfile(res.tempFilePath, that.srclist.length - 1);

				}
			})
		},
		/*选择图片*/
		chooseImage: function() {
			var that = this
			wx.chooseImage({
				sourceType: ['album', 'camera'],
				sizeType: ['compressed'],
				count: 9 - that.srclist.length,
				success: function(res) {

					for (var i = 0; i < res.tempFiles.length; i++) {
						that.srclist.push({
							path: res.tempFiles[i].path,
							type: 0
						});
						that.uploadfile(res.tempFiles[i].path, that.srclist.length - 1);
					}
				}
			})
		},
		showActionSheet: function() {
			var that = this;
			if (that.isImg == "") {
				wx.showActionSheet({
					itemList: ['上传图片', '上传视频'],
					success(res) {
						console.log(res.tapIndex)
						if (res.tapIndex == 0) {
							that.chooseImage();
							//that.isImg = 1;
						} else {
							that.chooseVideo();
							//that.isImg = 2;
						}
					},
					fail(res) {
						console.log(res.errMsg)
					}
				})
			} else if (that.isImg == 1) {
				that.chooseImage();
			} else {
				wx.showToast({
					icon: "none",
					title: "只能添加一个视频"
				});
			}

		},
		del: function(index) {
			var that = this;
			that.srclist.splice(index, 1);
			if (that.srclist.length == 0) {
				that.isImg = '';
			}
		},

		uploadfile: function(imgpath, index) {
			var that = this;
			wx.showToast({
				icon: "loading",
				title: "正在上传"
			});
			uni.uploadFile({
				url: api.upload,
				filePath: imgpath,
				name: 'file',
				success: function(uploadFileRes) {
					let img = JSON.parse(uploadFileRes.data).data.src;
					console.log(img);
					console.log(that.srclist[index])
					that.srclist[index].path = img;
					if (that.srclist[index].type == 0) {
						that.isImg = '1';
					} else {
						that.isImg = '2';
					}
				}
			});
		},

		/*添加实拍*/
		addVideolist: function() {
			var that = this;
			var param = {
				title: that.content,
			}
			if (this.isImg == 1) {
				let images_url = [];
				if (that.srclist.length > 0) {
					that.srclist.forEach(item => {
						if (item.type == 0) {
							images_url.push(item.path)
						}
					})
				}
				param.images_url = images_url;
			} else if (this.isImg == 2) {
				param.video_url = that.srclist[0].path
			}
			postajaxnologin(api.addvideo, param).then(da => {
				if (da.code == 200) {
					wx.showToast({
						icon: "none",
						title: da.msg
					});
					setTimeout(function() {
						uni.redirectTo({
							url: '/pages/video/video'
						})
					}, 500);
					that.content = '';
					that.srclist = [];
					that.isImg = '';
					that.videoSrc = '';
				}
			})


		},

		playvideo: function(path) {
			this.videoSrc = path;
			var videoContext = this.videoContext;
			videoContext.seek(0);
			videoContext.play();
			videoContext.requestFullScreen();
		},
		/**
		 * 全屏改变
		 */
		bindVideoScreenChange: function(e) {
			var status = e.mp.detail.fullScreen;

			if (!status) {
				var videoContext = this.videoContext;
				videoContext.pause();
				this.videoSrc = '';
			} else {

			}
			//this.setData(play);
		}

	},
	onReady: function(res) {
		this.videoContext = wx.createVideoContext('prew_video');
	},
}
